import React from 'react'
import ReactMarkdown from 'react-markdown'
import people from '../content/output/people.json'

const types = [
  { key: 'faculty', title: 'Faculty' },
  { key: 'postdoc', title: 'Post Doc' },
  { key: 'phd', title: 'PhD' },
  { key: 'master', title: 'Master' },
  { key: 'undergrad', title: 'Undergrad'},
  { key: 'alumni', title: 'Alumni' }
]

class People extends React.Component {
  componentDidMount() {
  }

  render() {
    console.log(this.props)
    return (
      <div id="people" >
        <h1>People</h1>
        { types.map((type) => {
          return (
            <div>
              <h2>{ type.title }</h2>
              <div className="ui grid">
                { people
                  .filter((person) => {
                    return person.type === type.key
                  }) // filter
                  .map((person) => {
                    return (
                      <div className="four wide column">
                        <img className="ui medium circular image" src="/images/wireframe/square-image.png"/>
                        <p><b>{ person.name }</b></p>
                        <p>{ person.title }</p>
                      </div>
                    ) // return
                  }) // map
                }
              </div>
            </div>
          )
        })}
      </div>
    )
  }
}

export default People
